.image-and-text {
    margin: 50px 0px;
    &.image-left-and-text {
        .content {
            left: -120px;
        }
    }
    &.image-right-and-text {
        .content {
            right: -120px;
        }
    }
    .content {
        padding: 85px 20px;
        position: absolute;
        left: 15px;
        right: 15px;
        top: 15%;
        z-index: 1;
        h3 {
            margin-bottom: 20px;
            font-weight: 800;
            max-width: 80%;
        }
        p {
            max-width: 80%;
        }
    }
}