.openingtimes-address-text {
    .column_1, .column_2 {
        h2, h3 {
            margin-bottom: 40px;
            font-weight: 600;
        }
        p {
            margin-bottom: 20px;
        }
    }
    .column_2 {
        padding-top: 100px;
        padding-bottom: 50px;
    }
    .column_1 {
        padding-top: 50px;
        padding-bottom: 50px;
        position: relative;
        height: 400px;
        .content {
            z-index: 1;
            position: absolute;
        }
        &::before {
            content: "";
            background: $blue;
            position: absolute;
            top: 0px;
            bottom: 0px;
            left: -200%;
            right: 0px;
            z-index: 0;
        }
    }
}