header {
  background-color: $green;
  padding: 10px 0px;
  .navbar-brand {
    img {
      height: 50px;
      object-fit: contain;
    }
  }
  .collapse.navbar-collapse {
    div:first-child {
      margin-left: auto;
      margin-right: auto;
    }
  }
  ul {
    padding: 0px 0px;
    margin: 0px 0px;
    li {
      display: inline-block;
      list-style: none;
      line-height: 35px;
      padding: 0px 10px;
      a {
        color: $white;
        font-weight: 600;
      }
    }
  }
  .languages {
    li {
      &.current-lang {
        text-decoration: underline;
      }
      a {
        text-transform: uppercase;
      }
    }
  }
}