.single-blog-post {
    padding: 50px 0px;
    .title {
        .date {
            margin-bottom: 20px;
            max-width: 800px;
            margin-left: auto;
            margin-right: auto;
        }
        h1 {
            max-width: 800px;
            margin-left: auto;
            margin-right: auto;
            &.otsikko-1 {

            }
            &.otsikko-2 {
                text-align: center;
                font-size: 5rem;
                line-height: 6rem;
            }
        }
    }
    .content {
        p, h5 {
            max-width: 600px;
            margin-left: auto;
            margin-right: auto;
            margin-bottom: 40px;
        }
        img {
            margin-bottom: 40px;
        }
    }
}