.frontpage-hero {
    height: 40vw;
    background-position: center;
    background-size: cover;
    h1 {
        position: absolute;
        bottom: 40px;
        left: 0px;
        font-size: 6rem;
        line-height: 7rem;
        font-weight: 800;
        color: $white;
        max-width: 600px;
    }
    .date {
        position: absolute;
        top: 80px;
        right: 0px;
        color: $white;
        text-decoration: underline;
        font-size: 3rem;
        line-height: 4rem;
        max-width: 160px;
    }
}

@media screen and (max-width: $breakpoint-lg) {
    .frontpage-hero {
        height: 60vh;
        h1 {
            position: absolute;
            bottom: 10vh;
            left: 15px;
            right: 15px;
            font-size: 2rem;
            line-height: 2.5rem;
        }
        .date {
            position: absolute;
            top: 80px;
            left: 15px;
            color: $white;
            text-decoration: underline;
            font-size: 3rem;
            line-height: 4rem;
            max-width: 160px;
        }
    }
}